<template>
  <div class="example-form">
    <div class="form-item">
      <label>First Name</label>
      <input type="text" class="form-item__input" v-model="firstName"/>
    </div>
    <div class="form-item">
      <label>Last Name</label>
      <input type="text" class="form-item__input" v-model="lastName"/>
    </div>
    <div class="form-item">
      <label>Full Name</label>
      <input type="text" class="form-item__input" v-model="fullName"/>
    </div>
  </div>
</template>

<script>
export default {
  name: "ComputedExample",
  props: {
  },
  data() {
    return {
      firstName: '',
      lastName: '',
    }
  },
  methods: {
  },
  computed: {
    fullName:{
      get: function() {
        return `${this.firstName} ${this.lastName}`
      },
      set: function(value) {
        const name = value.split(' ')
        this.firstName = name[0] === undefined ? '' : name[0]
        this.lastName = name[1] === undefined ? '' : name[1]
      } 
    }
  },
};
</script>

